  margin-left: 65px;
  font-weight: 500; }
  .text__title {
    margin-bottom: 25px;
    font-size: 2.5em; }
  .text__style {
    white-space: wrap;
    color: rgba(0, 0, 0, 0.4);
    margin-bottom: 40px;
    font-size: 1.39em; }

.subscribe {
  position: relative;
  margin-top: 10px; }
  .subscribe__input {
    margin: 5px;
    margin-left: 58px;
    border: 0 solid orange;
    border-left-width: 3px;
    border-top: none;
    border-right: none;
    border-bottom: none;
    width: 94%;
    height: 70px;
    position: relative;
    /*top:25px;
        left:25px;*/
    box-shadow: 0px 30px 20px rgba(0, 0, 0, 0.1); }
  .subscribe [placeholder] {
    padding: 20px;
    font-size: 1.3em;
    font-weight: 300; }
  .subscribe__arrow-right {
    position: relative;
    bottom: 55px;
    left: 95%;
    font-size: 2em;
    color: rgba(0, 0, 0, 0.3); }

.social {
  /*background:#c9f658;*/
  margin: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 55px; }
  .social__list {
    /*background:#9692af;*/
    display: flex;
    flex-direction: row; }
  .social__item {
    list-style: none; }
  .social__link {
    text-decoration: none; }
  .social__icon {
    /*display:block;*/
    position: relative;
    width: 40px;
    height: 40px;
    line-height: 50px;
    font-size: 1.57em;
    border-radius: 50%;
    margin-left: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #ffff;
    border: 1px solid #e7d6e3ce;
    color: gray; }
    .social__icon--1:hover {
      background: #fff;
      color: #55acee; }
    .social__icon--2:hover {
      background: #3b5999;
      color: #fff; }
    .social__icon--3:hover {
      background: #fff;
      color: #cd201f; }
    .social__icon--4:hover {
      color: #fff;
      background: linear-gradient(to right, #f46f30 60%, #c32aa3); }

.right {
  background: linear-gradient(135deg, #ffa953 0%, rgba(207, 54, 153, 0.61) 100%), rgba(246, 246, 246, 0.6);
  justify-content: center;
  align-items: center;
  flex-basis: 80%; }

.slider {
  width: 250px;
  height: 540px; }
  .slider__swiper {
    background-color: rgba(246, 246, 246, 0.3);
    display: flex;
    height: calc(100% / 3 - 10px);
    justify-content: center;
    align-items: center; }
    .slider__swiper:not(:nth-child(4)) {
      margin-bottom: 10px; }
    .slider__swiper:nth-child(2) {
      background: #fff; }
  .slider__card {
    /*background:blue;*/
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
  .slider__img {
    font-size: 2.3em;
    color: white;
    margin-bottom: 20px; }
    .slider__img--2 {
      color: rgba(155, 155, 155, 0.8); }
  .slider__title {
    color: white;
    text-align: center; }
  .slider__boton {
    font-size: 2em;
    color: white;
    position: relative; }
    .slider__boton--1 {
      bottom: 255px;
      left: 238px; }
    .slider__boton--2 {
      bottom: 210px;
      left: 211px; }

